 600;\n  text-transform: uppercase;\n  letter-spacing: 0.05em;\n  color: var(--gray-500);\n  margin-bottom: 0.5rem;\n  padding-left: 0.5rem;\n}\n\n.nav-item {\n  display: flex;\n  align-items: center;\n  gap: 0.75rem;\n  padding: 0.75rem;\n  border-radius: var(--radius);\n  color: var(--gray-700);\n  text-decoration: none;\n  transition: all 0.2s;\n  cursor: pointer;\n}\n\n.nav-item:hover {\n  background: var(--gray-100);\n}\n\n.nav-item:hover .todo-actions {\n  opacity: 1;\n}\n\n.nav-item.active {\n  background: var(--primary);\n  color: white;\n}\n\n.nav-item .badge {\n  margin-left: auto;\n  background: var(--gray-300);\n  color: var(--gray-700);\n  font-size: 0.75rem;\n  padding: 0.125rem 0.5rem;\n  border-radius: 9999px;\n}\n\n.nav-item.active .badge {\n  background: rgba(255, 255, 255, 0.2);\n  color: white;\n}\n\n/* Main Content */\n.main-content {\n  margin-left: 280px;\n  min-height: 100vh;\n  display: flex;\n  flex-direction: column;\n}\n\n.top-bar {\n  padding: 1rem 1.5rem;\n  border-bottom: 1px solid var(--gray-200);\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  background: white;\n}\n\n.page-title {\n  font-size: 1.5rem;\n  font-weight: 600;\n  color: var(--gray-900);\n}\n\n.quick-actions {\n  display: flex;\n  gap: 0.5rem;\n}\n\n.btn {\n  padding: 0.5rem 1rem;\n  border-radius: var(--radius);\n  border: none;\n  font-weight: 500;\n  cursor: pointer;\n  transition: all 0.2s;\n  display: inline-flex;\n  align-items: center;\n  gap: 0.5rem;\n}\n\n.btn-primary {\n  background: var(--primary);\n  color: white;\n}\n\n.btn-primary:hover {\n  background: var(--primary-dark);\n}\n\n.btn-secondary {\n  background: var(--gray-100);\n  color: var(--gray-700);\n}\n\n.btn-secondary:hover {\n  background: var(--gray-200);\n}\n\n/* Content Area */\n.content-area {\n  flex: 1;\n  padding: 1.5rem;\n  background: var(--gray-50);\n}\n\n.stats-grid {\n  display: grid;\n  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));\n  gap: 1rem;\n  margin-bottom: 1.5rem;\n}\n\n.stat-card {\n  background: white;\n  padding: 1.25rem;\n  border-radius: var(--radius);\n  box-shadow: var(--shadow);\n  display: flex;\n  align-items: center;\n  gap: 1rem;\n}\n\n.stat-icon {\n  width: 48px;\n  height: 48px;\n  border-radius: var(--radius);\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  font-size: 1.25rem;\n}\n\n.stat-icon.primary {\n  background: #dbeafe;\n  color: var(--primary);\n}\n.stat-icon.success {\n  background: #d1fae5;\n  color: var(--success);\n}\n.stat-icon.warning {\n  background: #fef3c7;\n  color: var(--warning);\n}\n.stat-icon.danger {\n  background: #fee2e2;\n  color: var(--danger);\n}\n\n.stat-info h3 {\n  font-size: 0.875rem;\n  color: var(--gray-500);\n  margin-bottom: 0.25rem;\n}\n\n.stat-value {\n  font-size: 1.5rem;\n  font-weight: 700;\n  color: var(--gray-900);\n}\n\n/* Todo List */\n.todo-list {\n  background: white;\n  border-radius: var(--radius);\n  box-shadow: var(--shadow);\n  overflow: hidden;\n}\n\n.todo-header {\n  padding: 1rem 1.5rem;\n  border-bottom: 1px solid var(--gray-200);\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n}\n\n.todo-item {\n  padding: 1rem 1.5rem;\n  border-bottom: 1px solid var(--gray-100);\n  display: flex;\n  align-items: center;\n  gap: 1rem;\n  transition: background 0.2s;\n}\n\n.todo-item:hover {\n  background: var(--gray-50);\n}\n\n.todo-checkbox {\n  width: 20px;\n  height: 20px;\n  border-radius: 50%;\n  border: 2px solid var(--gray-300);\n  cursor: pointer;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n}\n\n.todo-checkbox.checked {\n  background: var(--success);\n  border-color: var(--success);\n  color: white;\n}\n\n.todo-content {\n  flex: 1;\n}\n\n.todo-title {\n  font-weight: 500;\n  margin-bottom: 0.25rem;\n}\n\n.todo-title.completed {\n  text-decoration: line-through;\n  color: var(--gray-500);\n}\n\n.todo-meta {\n  display: flex;\n  gap: 1rem;\n  font-size: 0.875rem;\n  color: var(--gray-500);\n}\n\n.priority-badge {\n  padding: 0.125rem 0.5rem;\n  border-radius: 9999px;\n  font-size: 0.75rem;\n  font-weight: 600;\n}\n\n.priority-low {\n  background: #d1fae5;\n  color: #065f46;\n}\n.priority-medium {\n  background: #fef3c7;\n  color: #92400e;\n}\n.priority-high {\n  background: #fee2e2;\n  color: #991b1b;\n}\n.priority-critical {\n  background: #fce7f3;\n  color: #9d174d;\n}\n\n.todo-actions {\n  display: flex;\n  gap: 0.5rem;\n  opacity: 0;\n  transition: opacity 0.2s;\n}\n\n.todo-item:hover .todo-actions {\n  opacity: 1;\n}\n\n.action-btn {\n  width: 32px;\n  height: 32px;\n  border-radius: var(--radius);\n  border: none;\n  background: var(--gray-100);\n  color: var(--gray-600);\n  cursor: pointer;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  transition: all 0.2s;\n}\n\n.action-btn:hover {\n  background: var(--gray-200);\n}\n\n/* Empty State */\n.empty-state {\n  padding: 3rem 1.5rem;\n  text-align: center;\n  color: var(--gray-500);\n}\n\n.empty-state-icon {\n  font-size: 3rem;\n  color: var(--gray-300);\n  margin-bottom: 1rem;\n}\n\n/* Modal */\n.modal-overlay {\n  position: fixed;\n  top: 0;\n  left: 0;\n  right: 0;\n  bottom: 0;\n  background: rgba(0, 0, 0, 0.5);\n  display: none;\n  align-items: center;\n  justify-content: center;\n  z-index: 1000;\n}\n\n.modal-overlay.active {\n  display: flex;\n}\n\n.modal {\n  background: white;\n  border-radius: var(--radius);\n  box-shadow: var(--shadow-lg);\n  width: 90%;\n  max-width: 600px;\n  max-height: 90vh;\n  overflow-y: auto;\n}\n\n.modal-header {\n  padding: 1.5rem;\n  border-bottom: 1px solid var(--gray-200);\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n}\n\n.modal-body {\n  padding: 1.5rem;\n}\n\n.form-group {\n  margin-bottom: 1.5rem;\n}\n\n.form-label {\n  display: block;\n  font-size: 0.875rem;\n  font-weight: 500;\n  color: var(--gray-700);\n  margin-bottom: 0.5rem;\n}\n\n.form-input {\n  width: 100%;\n  padding: 0.625rem 0.75rem;\n  border: 1px solid var(--gray-300);\n  border-radius: var(--radius);\n  font-size: 0.875rem;\n  transition: border-color 0.2s;\n}\n\n.form-input:focus {\n  outline: none;\n  border-color: var(--primary);\n  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);\n}\n\n.modal-footer {\n  padding: 1.5rem;\n  border-top: 1px solid var(--gray-200);\n  display: flex;\n  justify-content: flex-end;\n  gap: 0.75rem;\n}\n\n/* Responsive */\n@media (max-width: 768px) {\n  .sidebar {\n    width: 100%;\n    position: static;\n    height: auto;\n  }\n\n  .main-content {\n    margin-left: 0;\n  }\n\n  .stats-grid {\n    grid-template-columns: 1fr;\n  }\n}\n"],"sourceRoot":""}]);
